
:root {
    --main-color: #ec2979;
    --color-dark: #1d2231;
    --text-grey: #8390A2;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    list-style-type: none;
    text-decoration: none;
    font-family: 'Poppins', sens-serif;
}
.sidebar {
    width: 250px;
    position: fixed;
    left: 0;
    top: 0;
    height: 100%;
    background-color: var(--main-color);
    z-index: 100;
    transition: margin-left 300ms;
}
.sidebar-brands {
    height: 90px;
    padding: 1rem 0rem 1rem 1rem;
    color: #fff;
}
.sidebar-brands span {
    display: inline-block;
    padding-right: 1rem;
}
.sidebar-menu {
    margin-top: 1rem;
}
.sidebar-menu li {
    width: 100%;
    margin-bottom: 1.7rem;
}
.sidebar-menu a{
    padding-left: 1rem;
    display: block;
    color: #fff;
    font-size: 1.1rem;
}
.sidebar-menu a.active-admin {
    background-color: #fff;
    padding-top: 1rem;
    padding-bottom: 1rem;
    color: var(--main-color);
    border-radius: 30px 0px 0px 30px;
}
.sidebar-menu a span:first-child {
    font-size: 1.5rem;
    padding-right: 1rem;
}

#nav-toggle:checked + .sidebar {
    width: 70px;
}
#nav-toggle:checked + .sidebar .sidebar-brands,
#nav-toggle:checked + .sidebar li  {
    padding-left: 1rem;
    text-align: center;
}
#nav-toggle:checked + .sidebar li a{
    padding-left: 0rem;
}

#nav-toggle:checked + .sidebar .sidebar-brands h2 span:last-child,
#nav-toggle:checked + .sidebar li a span:last-child {
    display: none;
}
#nav-toggle:checked ~ .main-content {
    margin-left: 70px;
}
#nav-toggle:checked ~ .main-content header {
    width: calc(100% - 70px);
    left: 70px;
}
.main-content{
    transition: margin-left 300ms;
    margin-left: 250px;
}
header {
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 1.5rem;
    box-shadow: 2px 2px 5px rgba(0,0,0,0.2);
    position: fixed;
    width: calc(100% - 250px);
    top: 0;
    z-index: 100;
    transition: left 300ms;
}
#nav-toggle {
    display: none;
}
header h2 {
    color: #222;
    
}
header label span {
    font-size: 1.4rem;
    padding-right: 1rem;
}
.search-wrapper {
    border: 1px solid #acc;
    border-radius: 30px;
    height: 50px;
    display: flex;
    align-items: center;
    overflow-x: hidden;
}
.search-wrapper span {
    display: inline-block;
    padding: 0rem 1rem;
}
.search-wrapper input {
    height: 100%;
    padding: .5rem;
    border: none;
    outline: none;
}
.user-wrapper {
    display: flex;
    align-items: center;
}
.user-wrapper h4 {
    margin-bottom:  0rem !important ;
}
.user-wrapper small {
    display: inline-block;
    color: var(--text-grey)
}
main{
    margin-top: 95px;
    padding: 2rem 1.5rem;
    background: #f1f5f9;
    min-height: calc(100vh - 95px);
}
.cards {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 2rem;
    margin-top: 1rem;
}

.card-single {
    display: flex;
    justify-content: space-between;
    background: #fff;
    padding: 2rem;
    border-radius: 2px;
}
.card-single span:first-child{
    color: var(--text-grey)
}
.card-single div:last-child span {
    color: var(--main-color);
    font-size: 3rem;
}
.card-single:last-child {
    background: var(--main-color);
}
.card-single:last-child h1,
.card-single:last-child div:last-child span,
.card-single:last-child div:last-child span{
    color: #fff;
}
.recent-grid {
    margin-top: 3.5rem;
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: 70% auto;
}

.testi {
    margin-top: 3.5rem;
    display: grid;
    grid-gap: 2rem;
    grid-template-columns: 100% auto;
    
}
.card {
    background: #fff;
    border-radius: 5px;
}
.card-header,
.card-body{
    padding: 1rem;
}
.card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #f0f0f0;
}
.test-card-header {
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #f0f0f0;
}
/* .userlists-header {
    display: flex;
    justify-content: center;
    align-items: center;
} */
.btn-dash {
    background: var(--main-color);
    border-radius: 10px;
    color: #fff;
    font-size: .8rem;
    padding: .5rem 1rem;
    border: 1px solid var(--main-color);
}

.table-responsive table td button {
    background: var(--main-color);
    border-radius: 10px;
    color: #fff;
    font-size: .8rem;
    padding: .5rem 1rem;
    border: 1px solid var(--main-color);
}
.table-responsive table {
    border-collapse: collapse;
    width: 100%;
}
.table-responsive table thead tr {
    border-bottom: 1px solid #f0f0f0;
    border-top: 1px solid #f0f0f0;
}
.table-responsive table thead td {
    font-weight: 700;
    
}

.table-responsive table td {
    padding: .3rem 1rem;
    font-size: .9rem;
    color: #222;
}
.table-responsive table td .status {
    display: inline-block;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    background-color: #f44336;
    padding-top: 20px;
}
.table-responsive{
    width: 100%;
}
.customer {
    display: flex;
    justify-content: space-between;
    padding: .5rem .7rem;
}
.info {
    display: flex;
    align-items: center;
}
.info h4 {
    font-size: .8rem;
    font-weight: 700;
    color: #222;
}
.info small{
    font-weight: 600;
    color: var(--text-grey);
}
.contact {
    font-size: 1.2rem;
    display: inline-block;
    margin-left: .5rem;
    color: var(--main-color)
}
.userlists {
    display: flex;
    justify-content: center;
    margin-top: 7%;
    width: calc(100% - 250px);
}
.userlists h3{
    margin: 0 auto;
}
.txtName {
    white-space: -moz-pre-wrap !important;  /* Mozilla, since 1999 */
    white-space: -webkit-pre-wrap;          /* Chrome & Safari */ 
    white-space: -pre-wrap;                 /* Opera 4-6 */
    white-space: -o-pre-wrap;               /* Opera 7 */
    white-space: pre-wrap;                  /* CSS3 */
    word-wrap: break-word;                  /* Internet Explorer 5.5+ */
    word-break: break-all;
    white-space: normal;
}
@media only screen and (max-width: 1200px){
    .sidebar {
        width: 70px;
    }
    .sidebar .sidebar-brands,
    .sidebar li  {
        padding-left: 1rem;
        text-align: center;
    }
    .sidebar li a{
        padding-left: 0rem;
    }

    .sidebar .sidebar-brands h2 span:last-child,
    .sidebar li a span:last-child {
        display: none;
    }
    .main-content {
        margin-left: 70px;
    }
    .main-content header {
        width: calc(100% - 70px);
        left: 70px;
    }
    .userlists { 
        width: calc(100% - 70px);
        left: 70px;
        margin-left: auto;
    }
    .sidebar:hover {
        width: 250px;
        z-index: 200;
    }
    .sidebar:hover .sidebar-brands,
    .sidebar:hover li  {
        padding-left: 2rem;
        text-align: left;
    }
    .sidebar:hover li a{
        padding-left: 1rem;
    }

    .sidebar:hover .sidebar-brands h2 span:last-child,
    .sidebar:hover li a span:last-child {
        display: inline;
    }
    
}
@media only screen and (max-width: 1070px){
    .recent-grid {
        grid-template-columns: 70% auto;
    }
}
@media only screen and (max-width: 960px){
    .cards {
        grid-template-columns: repeat(3, 1fr);
    }
    .recent-grid {
        grid-template-columns: 70% auto;
    }
}
@media only screen and (max-width: 768px){
    .cards {
        grid-template-columns: repeat(3, 1fr);
    }
    .recent-grid {
        grid-template-columns: 70% auto;
    }
    .testi {
    grid-template-columns: 100% auto;
    
    }
    .search-wrapper {
        display: none;
    }
    .sidebar {
        left: -100% !important;
    }
    header h2 label {
        display: inline-block;
        background: var(--main-color);
        padding-right: 0rem;
        margin-right: 1rem;
        height: 40px;
        width: 40px;
        border-radius: 50%;
        color: #fff;
        display: flex;
        align-items: center;
        justify-content: center !important;
    }
   
    header h2 span {
        text-align: center;
        padding-right: 0rem;
    }
    header h2 span{
        display: none;
    }
    .main-content {
        width: 100%;
        margin-left: 0rem;
    }
    header {
        width: 100%  !important;
        left: 0 !important;
    }
    .userlists { 
         width: 100%  !important;
        left: 0 !important;
        right: 0 !important;
        position: absolute;
        margin-left: .5rem !important;
    }
    .userlists .table{
        width: 70%;
    }
    #nav-toggle:checked + .sidebar{
        left: 0 !important;
        z-index: 100;
        width: 250px;
    }
    #nav-toggle:checked + .sidebar .sidebar-brands,
    #nav-toggle:checked + .sidebar li  {
        padding-left: 2rem;
        text-align: left;
    }
    #nav-toggle:checked + .sidebar li a{
        padding-left: 1rem;
    }
    #nav-toggle:checked + .sidebar .sidebar-brands h2 span:last-child,
    #nav-toggle:checked + .sidebar li a span:last-child {
        display: inline;
    }
    #nav-toggle:checked ~ .main-content {
        margin-left: 0rem !important;
    }
    
}
@media only screen and (max-width: 560px){
    .cards {
        grid-template-columns: 100%;
    }
    .testi {
    grid-template-columns: 100% auto;
    
    }
}
